import { useEffect, useState } from "react";
import QRCode from "react-qr-code";
import "./qr-code.css";
const QRcode = () => {
  const [image, setImageURL] = useState("");
  const [restauranId, setRestauranId] = useState("");
  useEffect(() => {
    const { _id, imageURL } = JSON.parse(localStorage.getItem("user"));
    setImageURL(imageURL);
    setRestauranId(_id);
  }, [setImageURL, setRestauranId]);

  return (
    <div className="qr-code-page">
      <div className="qr-code-container">
        <div className="logo-qr-bg logo-qr-client">
        <img
            className="logo-qr"
            src="/img/Group73.png"
            alt="Loading..."
          ></img>
          <div
            style={{
              backgroundImage: 'url("")',
            }}
            className="logo-qr"
          ></div>
        </div>
        <div className="logo-qr-bg logo-qr-beep">
          <img
            className="logo-qr"
            src={`http://localhost:4000/static/images/${image}`}
            alt="Loading..."
          ></img>
          <div
            style={{
              backgroundImage: `url("")`,
            }}
            className="logo-qr"
          />
        </div>
        <QRCode
          size={400}
          className="qrcode"
          value={`https://salty-river-10867.herokuapp.com/restaurant/beep/${restauranId}`}
        />
      </div>
    </div>
  );
};

export default QRcode;
